<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础使用1">基础使用</a></li><li><a href="#vue-doc-自动转换1">自动转换</a></li><li><a href="#vue-doc-综合设置1">综合设置</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-carousel走马灯1" class="vue-doc-element">Carousel 走马灯</h1>
<p class="vue-doc-element">常用于一组图片或卡片轮播，当内容空间不足时，可以用走马灯的形式进行收纳，进行轮播展现。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demoautoplay></demoautoplay>
<demosetting></demosetting>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>幻灯片的索引，从 0 开始，可以使用 v-model 双向绑定数据</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>height</td>
<td>走马灯的高度，可填 auto 或具体高度数值，单位 px</td>
<td>String / Number</td>
<td>auto</td>
</tr>
<tr>
<td>loop</td>
<td>是否开启循环</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autoplay</td>
<td>是否自动切换</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autoplay-speed</td>
<td>自动切换的时间间隔，单位为毫秒</td>
<td>Number</td>
<td>2000</td>
</tr>
<tr>
<td>dots</td>
<td>指示器的位置，可选值为 inside （内部），outside（外部），none（不显示）</td>
<td>String</td>
<td>inside</td>
</tr>
<tr>
<td>radius-dot</td>
<td>是否显示圆形指示器</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>trigger</td>
<td>指示器的触发方式，可选值为 click（点击），hover（悬停）</td>
<td>String</td>
<td>click</td>
</tr>
<tr>
<td>arrow</td>
<td>切换箭头的显示时机，可选值为 hover（悬停），always（一直显示），never（不显示）</td>
<td>String</td>
<td>hover</td>
</tr>
<tr>
<td>easing</td>
<td>动画效果</td>
<td>String</td>
<td>ease</td>
</tr>
</tbody>
</table>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>幻灯片切换时触发，目前激活的幻灯片的索引，原幻灯片的索引</td>
<td>oldValue, value</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demoautoplay from './demo/autoplay.md';
import demosetting from './demo/setting.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demoautoplay,demosetting
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


